import React, { Component } from 'react';
import { CustomAuth } from './CustomAuth';
import * as Consts from '../../shared/Consts';
import _ from 'lodash';
import { Auth } from 'aws-amplify';

export default class AuthWrapper extends Component {
  constructor(props) {
    super(props);
    this.updateUsername = this.updateUsername.bind(this);
    this.updateUser = this.updateUser.bind(this);

    this.state = {
      username: '',
      emailVerified: null,
      user: null,
    };
  }

  updateUsername(newUsername) {
    this.setState({ username: newUsername });
  }

  updateUser(user) {
    const emailVerified = _.get(user, 'attributes.email_verified', false);
    this.setState({ user, emailVerified });
  }

  async componentDidMount() {
    if (this.props.authState === Consts.AUTH_STATUS_SIGN_IN) {
      this.setState({ user: null, emailVerified: null });
    }

    try {
      const user = await Auth.currentAuthenticatedUser();
      if (user) {
        const userAttributes = await Auth.userAttributes(user);
        const emailVerifiedAttr = _.find(userAttributes, {
          Name: 'email_verified',
        });
        const emailVerified =
          _.get(emailVerifiedAttr, 'Value', 'false') === 'true';
        this.setState({ user, emailVerified });
      }
    } catch (err) {
      if (err === 'The user is not authenticated') {
        // Todo
      } else {
        throw err;
      }
    }
  }

  render() {
    const ChildComponent = this.props.ChildComponent;

    let authState = this.props.authState;
    if (this.state.user) {
      if (authState !== Consts.AUTH_STATUS_SIGN_IN) {
        authState =
          this.state.emailVerified === false
            ? Consts.AUTH_STATUS_EMAIL_NOT_VERIFIED
            : authState;
      }
    }

    return (
      <>
        <div style={{ minHeight: '85vh' }}>
          <CustomAuth
            authState={authState}
            updateUsername={this.updateUsername}
            updateUser={this.updateUser}
            onStateChange={this.props.onStateChange}
            history={this.props.history}
          />
          {authState === Consts.AUTH_STATUS_SIGNED_IN &&
            this.state.emailVerified !== null && (
              <ChildComponent {...this.props} />
            )}
        </div>
      </>
    );
  }
}
